.create-event-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: auto;
  z-index: 1000;
}

.create-event-modal .create-event-modal-content {
  background: white;
  padding: 20px;
  border-radius: 5px;
  width: 500px;
  max-width: 90%;
}

.create-event-modal .create-event-modal-content input,
.create-event-modal .create-event-modal-content textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border-radius: 5px;
  border: 1px solid #ddd;
  font-size: 1rem;
  box-sizing: border-box;
}

.create-event-modal .create-event-modal-content textarea {
  height: 200px;
  width: 300px;
  resize: none;
}

.create-event-modal .create-event-modal-content button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
  margin-right: 10px;
}

.create-event-modal .create-event-modal-content button:hover {
  background-color: #0056b3;
}

.create-event-modal .create-event-modal-content button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

.create-event-modal .create-event-modal-content button:focus {
  outline: none;
}

.create-event-modal .create-event-modal-content form {
  display: flex;
  flex-direction: column;
}

h2 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 2rem;
  color: #000000;
}

ul {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  margin: 0;
}

li {
  background: #f9f9f9;
  border: 1px solid #ddd;
  padding: 15px;
  border-radius: 5px;
  margin-bottom: 10px;
  transition: background 0.3s ease;
  width: 350px;
  max-width: 500px;
}

li:hover {
  background: #f0f0f0;
}

h3 {
  margin: 0;
  font-size: 1.5rem;
  color: #555;
}

p {
  margin: 5px 0;
  font-size: 1rem;
  color: #000000;
}

button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
  margin-right: 10px;
}

button:hover {
  background-color: #0056b3;
}

button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

button:focus {
  outline: none;
}

button+button {
  margin-left: 10px;
}

div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  margin-top: 20px;
}

p.no-events {
  text-align: center;
  color: #999;
  font-size: 1.2rem;
}

@media (max-width: 768px) {
  h2 {
    font-size: 1.75rem;
  }

  h3 {
    font-size: 1.25rem;
  }

  li {
    padding: 12px;
  }

  button {
    padding: 8px 12px;
    font-size: 0.9rem;
  }

  .modal-content {
    width: 400px;
    max-width: 85%;
  }
}

@media (max-width: 576px) {
  h2 {
    font-size: 1.5rem;
  }

  h3 {
    font-size: 1.2rem;
  }

  li {
    padding: 10px;
  }

  button {
    padding: 7px 10px;
    font-size: 0.85rem;
  }

  .modal-content {
    width: 320px;
    max-width: 90%;
  }

  p.no-events {
    font-size: 1rem;
  }
}

@media (max-width: 400px) {
  h2 {
    font-size: 1.2rem;
  }

  h3 {
    font-size: 1rem;
  }

  li {
    padding: 8px;
  }

  button {
    padding: 5px 8px;
    font-size: 0.8rem;
  }

  .modal-content {
    width: 280px;
    padding: 15px;
  }

  p.no-events {
    font-size: 0.9rem;
  }
}

form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  background: #fff;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

input[type="text"],
input[type="email"],
input[type="password"] {
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
  width: 100%;
  box-sizing: border-box;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="password"]:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.3);
}

label {
  font-size: 1rem;
  color: #333;
  margin-bottom: 5px;
}

p[role="alert"] {
  color: red;
  font-size: 0.9rem;
  margin-top: 10px;
}

h1 {
  font-size: 3rem;
  color: #333;
  text-align: center;
  margin-bottom: 20px;
  font-family: 'Arial', sans-serif;
}


.calendar-button {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1000;
}




.fc {
  height: 100% !important;
}

.fc .fc-view {
  height: 100% !important;
}

.fc .fc-scroller {
  height: auto !important;
}

.fc .fc-daygrid-body {
  height: auto !important;
}

.fc .fc-daygrid-body-balanced {
  height: auto !important;
}

.calendar-event {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.calendar-event p {
  margin: 0;
  font-size: 0.9em;
  opacity: 0.8;
}

.fc td, .fc th {
  border: 1px solid #ddd !important;
}

.fc .fc-daygrid-day-number {
  padding: 4px;
  position: relative;
  z-index: 4;
  color: #333;
}

.fc .fc-daygrid-day {
  height: 80px !important;
}

.fc-daygrid-event {
  padding: 2px 4px !important;
  margin: 1px 2px !important;
  font-size: 0.85em !important;
}